$bg-color: #F6F7FB;
$text-color: #495057;
$description-color: #838999;
$btn-color: #726DC1;
$input-color: #E7EBF6;
$focus-input-color: #dde5f8;
$input-text-color: #28282c;
$transition-standard: 0.15s linear;
$standard-height: calc(100vh - 131px);

@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&family=Space+Mono:ital@0;1&display=swap');

$black-90: rgba(0, 0, 0, 0.9);
$black-80: rgba(0, 0, 0, 0.8);
$black-76: rgba(0, 0, 0, 0.76);
$black-67: rgba(0, 0, 0, 0.67);
$black-50: rgba(0, 0, 0, 0.5);
$black-40: rgba(0, 0, 0, 0.4);
$black-24: rgba(0, 0, 0, 0.24);
$black-20: rgba(0, 0, 0, 0.2);
$black-12: rgba(0, 0, 0, 0.12);
$black-8: rgba(0, 0, 0, 0.08);
$light-gray: #F2F2F0;
$white-100: rgba(255, 255, 255, 1);
$white-90: rgba(255, 255, 255, 0.9);
$white-80: rgba(255, 255, 255, 0.8);
$white-70: rgba(255, 255, 255, 0.7);
$white-60: rgba(255, 255, 255, 0.6);


@font-face {
  font-family: "SF Pro Display";
  src: url("./fonts/SF-Pro-Display-Medium.otf") format("otf");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "SF Pro Display";
  src: url("./fonts/SF-Pro-Display-Regular.otf") format("otf");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "SF Pro Text";
  src: url("./fonts/SF-Pro-Text-Medium.otf") format("otf");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "SF Pro Text";
  src: url("./fonts/SF-Pro-Text-Regular.otf") format("otf");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "SF Pro Text";
  src: url("./fonts/SF-Pro-Text-RegularItalic.otf") format("otf");
  font-weight: 400;
  font-style: italic;
  font-display: swap;
}


$f-family-sf-display: "SF Pro Display", sans-serif;
$f-family-sf-text: "SF Pro Text", sans-serif;


$f-family-roboto: 'Roboto', sans-serif;
$f-family-mono: 'Space Mono', monospace;

@mixin font-style( $f-family, $f-style, $fw,$f-size, $l-height,  $l-spacing, $color) {
  font-family: $f-family;
  font-style: $f-style;
  font-weight: $fw;
  font-size: $f-size;
  line-height: $l-height;
  letter-spacing: $l-spacing;
  color: $color;
}


@mixin fontStyle($f-size, $lh, $fw, $mb, $color) {
  color: $color;
  font-size: $f-size;
  font-weight: $fw;
  margin-bottom: $mb;
  line-height: $lh;
  font-style: normal;
}

%flex-element-center {
  display: flex;
  justify-content: center;
  align-items: center;
}


* {
  margin: 0;
  padding: 0;
  list-style: none;
  box-sizing: border-box;
}

.main__container {
  margin: 25px 0;
  background: $light-gray;
  height: calc(100vh - 50px);
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.login__container {
  @extend %flex-element-center;
  flex-direction: column;
  height: 100vh;
  background: transparent;


  img {
    width: 56px;
    height: 56px;
  }

  h1 {
    @include font-style($f-family-sf-display, normal, 400, 28px, 40px, 0.2px, $black-90);
    margin: 14px 0 27px;
  }

  button {
    width: 230px;
    @extend %flex-element-center;
    @include font-style($f-family-sf-text, normal, 400, 16px, 24px, 0.3px, $white-100);
    background: $black-90;
    border-radius: 12px;
    border: none;
    cursor: pointer;
    transition: $transition-standard;
    margin-bottom: 48px;
    padding: 16px 28px;
    justify-content: space-between;

    img {
      width: 20px;
      height: 20px;
    }

    &:hover {
      background-color: $black-80;
    }
  }

  span {
    @include font-style($f-family-mono, normal, 400, 14px, 24px, -0.01em, $black-50);
  }
}

nav {

  img {
    width: 40px;
    height: 40px;
    margin-bottom: 24px;
    margin-left: 15px;
  }

  .nav__container {
    padding-left: 15px;
    align-items: flex-start;
    display: flex;
    flex-direction: column;
    margin-right: 60px;
    @include font-style($f-family-mono, normal, 400, 14px, 24px, -0.01em, $black-50);

    .active_link {
      color: $black-90;
    }

    li {
      transition: $transition-standard;
      cursor: pointer;
      position: relative;
      margin-bottom: 10px;

      a {
        width: 100%;
        height: 100%;
      }

      &:hover {
        color: $black-67;
      }
    }

    .button__logOut {
      margin-top: 24px;
    }
  }
}

.users__container_imgContainer {
  position: relative;
  margin-right: 12px;
  border-radius: 50%;

  .userPhoto {
    border-radius: 50%;
    @include font-style($f-family-sf-display, normal, 500, 22px, 24px, 0.35px, $white-100);
    @extend %flex-element-center;
  }

  .users__container_status {
    position: absolute;
    width: 15px;
    height: 15px;
    padding: 2px;
    background: #F6F7FB;
    border: none;
    bottom: 0;
    right: 0;
  }
}


.users__container {
  display: flex;
  flex-direction: column;
  padding: 20px 6px;
  background: $white-100;
  border-radius: 24px;
  width: 550px;
  max-height: 100%;


  li {
    transition: $transition-standard;
    cursor: pointer;
    padding: 15px 14px 0 14px;
    border-radius: 12px;
    position: relative;

    &:last-child {
      .nameAndInfo__container {
        border: none;
      }
    }

    a {
      @extend %flex-element-center;
      justify-content: space-between;
      text-decoration: none;
      position: relative;
      align-items: flex-start;

    }

    &:hover {
      background: #F7F7F5;
    }

    div {
      display: flex;
      align-items: center;
    }

    img {
      width: 48px;
      height: 48px;
      border-radius: 50%;

    }

    .message {
      width: 32px;
      height: 32px;
      border: 1px solid $black-12;
      border-radius: 10px;
      background: transparent;
      transition: $transition-standard;
      cursor: pointer;
      @extend %flex-element-center;

      img {
        width: 20px;
        height: 20px;
        margin: 0;
      }

      &:hover {
        border: 1px solid rgba(0, 0, 0, 0.24);
      }
    }

    .nameAndInfo__container {
      display: flex;
      justify-content: space-between;
      width: 100%;
      padding-bottom: 15px;
      border-bottom: 0.5px solid $black-12;


      div {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        @include font-style($f-family-sf-text, normal, 500, 16px, 24px, -0.32px, $black-90);

        .message__container {
          @include font-style($f-family-sf-text, normal, 400, 14px, 24px, -0.154px, $black-50);

        }

        span {
          @include font-style($f-family-sf-text, italic, 400, 14px, 24px, -0.154px, $black-50);
        }
      }

    }


    .users__container_date {
      div {
        @extend %flex-element-center;
        justify-content: flex-end;
        width: 20px;
        height: 20px;
        padding-right: 1.1px;
        margin-right: 3px;

        .readImg {
          height: 20px;
        }

        .unReadImg {
          height: 20px;
        }
      }

      position: absolute;
      top: -2px;
      right: 0;
      @include font-style($f-family-sf-text, normal, 400, 14px, 24px, -0.154px, $black-50);
    }

    .unreadCounter {

      @extend %flex-element-center;
      min-width: 20px;
      height: 20px;
      border-radius: 50%;
      background: $black-90;
      @include font-style($f-family-sf-text, normal, 400, 14px, 24px, -0.154px, $white-100);
      position: absolute;
      right: 0;
      bottom: 17.5px;


    }

  }


}

.users__inputContainer {
  @extend %flex-element-center;
  position: relative;
  display: flex;
  margin: 0 14px 16px 14px;

  svg {

    position: absolute;
    width: 20px;
    height: 20px;
    left: 12px;

    path {
      transition: $transition-standard;
    }
  }

  input {
    width: 100%;
    @include font-style($f-family-sf-text, normal, 400, 14px, 24px, -0.154px, $black-90);
    border: none;
    padding: 12px 12px 12px 38px;
    transition: $transition-standard;
    background: $light-gray;
    border-radius: 12px;


    &::placeholder {
      transition: $transition-standard;
      @include font-style($f-family-sf-text, normal, 400, 14px, 24px, -0.154px, $black-50);
    }

    &:focus {
      outline: none;

      &::placeholder {
        @include font-style($f-family-sf-text, normal, 400, 14px, 24px, -0.154px, $black-40);
      }


    }
  }


}

.userProfile__container {
  min-width: 550px;
  display: flex;
  padding: 40px 52px 44px 48px;
  background: $white-100;
  max-width: 500px;
  border-radius: 24px;
  margin-left: 40px;
  position: relative;

  .userProfile__mainContainer {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    margin-left: 40px;
  }

  h1 {
    @include font-style($f-family-sf-display, normal, 400, 28px, 40px, 0.36px, $black-90);
    margin-bottom: 12px;
  }


  a {
    text-decoration: none;
    @extend %flex-element-center;
    border: none;
    border-radius: 10px;
    background-color: $black-90;
    transition: $transition-standard;
    @include font-style($f-family-sf-text, normal, 400, 14px, 24px, -0.154px, $white-100);
    justify-content: space-between;
    padding: 6px 12px 6px 10px;
    cursor: pointer;
    margin-top: 24px;

    img {
      margin-right: 6px;
    }

    &:hover {
      background-color: $black-76;
    }
  }

  .message {
    position: absolute;
    width: 32px;
    height: 32px;
    border: 1px solid $black-12;
    border-radius: 10px;
    background: transparent;
    transition: $transition-standard;
    cursor: pointer;
    @extend %flex-element-center;
    padding: 0;
    top: 16px;
    right: 16px;
    margin: 0;

    &:hover {
      background: transparent;
      border: 1px solid rgba(0, 0, 0, 0.24);
    }

    img {
      margin: 0;
    }
  }

  .hiddenEmail {
    @include font-style($f-family-mono, italic, 400, 14px, 24px, -0.01em, $black-50);
  }

  ul {

    width: 100%;
    display: flex;
    flex-direction: column;
    background: transparent;
    position: relative;

    li {
      display: flex;
      align-items: center;
      width: 100%;

      @include font-style($f-family-mono, normal, 400, 14px, 24px, -0.01em, $black-90);

      div {
        padding: 12px 0 12.5px 0;
        border-bottom: 0.5px solid $black-12;
        width: 100%;
      }

      img {
        margin-right: 12px;
      }

      &:last-child {
        div {
          border-bottom: none;
        }
      }
    }
  }
}

.editProfile__mainContainer {
  width: 100%;

  .containerWithButton {
    display: flex;
    margin-top: 36px;

    .save_button {
      @extend %flex-element-center;
      border: none;
      border-radius: 10px;
      background-color: $black-90;
      transition: $transition-standard;
      @include font-style($f-family-sf-text, normal, 400, 14px, 24px, -0.154px, $white-100);
      padding: 4px 12px;
      cursor: pointer;
      margin-right: 8px;

      &:hover {
        background-color: $black-76;
      }

    }

    .cancel_button {
      @extend %flex-element-center;
      border: 1px solid $black-12;
      border-radius: 10px;
      background-color: transparent;
      transition: $transition-standard;
      @include font-style($f-family-sf-text, normal, 400, 14px, 24px, -0.154px, $black-90);
      padding: 4px 12px;
      cursor: pointer;

      &:hover {
        border: 1px solid $black-24;
      }
    }
  }


  ul {
    width: 100%;
    display: flex;
    flex-direction: column;
    background: transparent;
    position: relative;


    .not_allow {
      pointer-events: none;
    }

    li {
      position: relative;
      display: flex;
      align-items: center;
      width: 100%;

      input {
        padding: 12px 0 12.5px 0;
        border: none;
        border-bottom: 0.5px solid $black-12;
        width: 100%;
        @include font-style($f-family-mono, normal, 400, 14px, 24px, -0.01em, $black-90);
        transition: $transition-standard;

        &:hover {
          border-bottom: 0.5px solid $black-90;
        }


        &::placeholder {
          transition: $transition-standard;
          @include font-style($f-family-mono, italic, 400, 14px, 24px, -0.01em, $black-50);
        }

        &:focus {
          outline: none;

          &::placeholder {
            color: $black-40;
          }
        }

      }

      img {
        margin-right: 12px;
      }

      &:last-child {
        div {
          border-bottom: none;
        }
      }
    }

    .editProfile__privateButton {
      width: 40px;
      height: 40px;
      margin: 0;
      @extend %flex-element-center;
      padding: 10px;
      border: none;
      border-radius: 50%;
      transition: $transition-standard;
      cursor: pointer;
      position: absolute;
      right: 0;
      background: transparent;

      .privateValue, .publicValue {
        path {
          transition: $transition-standard;
        }
      }


      &:hover {
        .privateValue {
          path {
            fill: rgba(0, 0, 0, 0.57);
          }
        }

        .publicValue {
          path {
            fill: rgba(0, 0, 0, 0.76);;
          }

        }
      }

    }
  }
}

.chatPage {
  position: relative;
  background: $white-100;
  min-width: 550px;
  display: flex;
  flex-direction: column;
  border-radius: 24px;
  max-height: calc(100vh - 48px);


  .Chat__userInfo {
    display: flex;
    padding: 9px 20px 7px 20px;
    @extend %flex-element-center;
    justify-content: space-between;
    border-bottom: 1px solid #E7EBF6;
    position: relative;
    max-height: 61px;

    .Chat__userInfoNameContainer {
      justify-content: center;
      width: 100%;
      position: relative;
      left: 20px;

      .isTyping {
        font-style: italic;
      }

      div {
        @include font-style($f-family-sf-text, normal, 400, 14px, 24px, -0.154px, $black-50);
        display: flex;
        flex-direction: column;
        align-items: center !important;

        h3 {
          margin: 0 !important;
          @include font-style($f-family-sf-text, normal, 500, 16px, 24px, -0.32px, $black-90);
        }
      }
    }

    .Chat__userInfo_searchBlock {
      transform: scaleX(0);
      transform-origin: right;
      width: calc(100% - 40px);
      height: 44px;
      position: absolute;
      border-radius: 12px;
      @extend %flex-element-center;
      background-color: white;
      transition: $transition-standard;
      justify-content: space-between;

      svg {
        position: absolute;
        left: 12px;
      }


      button {
        @include font-style($f-family-sf-text, normal, 400, 14px, 24px, -0.154px, $black-50);
        background: none;
        border: none;
        transition: $standard-height;
        cursor: pointer;
        margin-left: 16px;

        &:hover {
          color: $black-67;
        }
      }

      input {
        width: 453px;
        background-color: #F2F2F0;
        padding: 10px 12px 10px 38px;
        @include font-style($f-family-sf-text, normal, 400, 14px, 24px, -0.154px, $black-90);
        border-radius: 12px;
        border: none;
        transition: $transition-standard;

        &:hover {

        }

        &::placeholder {
          @include font-style($f-family-sf-text, normal, 400, 14px, 24px, -0.154px, $black-50);
        }

        &:focus {
          outline: none;

          &::placeholder {
            @include font-style($f-family-sf-text, normal, 400, 14px, 24px, -0.154px, $black-40);
          }


        }


      }
    }

    .Chat__userInfo_searchBlock_visible {
      transform: scaleX(1);
    }

    .buttonBack {
      @include font-style($f-family-sf-text, normal, 400, 14px, 24px, -0.154px, $black-50);
      background: transparent;
      border: none;
      @extend %flex-element-center;
      cursor: pointer;

      &:hover {
        color: $black-67;
      }

      img {
        margin-right: 6px;
      }
    }

    .rightBlock {
      @extend %flex-element-center;

      img {
        border-radius: 50%;
      }

      .Chat__userInfo_searchImg {
        min-width: 40px;
        height: 40px;
        margin-right: 5px;
        background: transparent;
        border: none;
        border-radius: 50%;
        transition: $transition-standard;
        cursor: pointer;

        svg {
          path {
            transition: $transition-standard;
          }
        }

        &:hover {
          svg {
            path {
              fill-opacity: 0.67;
            }
          }
        }
      }
    }

  }

  .pinContainer {
    padding: 10px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    button {
      border: none;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      margin: 0 !important;
      padding: 0 !important;
      cursor: pointer;
      background: transparent;

      svg {
        width: 20px;
        height: 20px;

        path {
          transition: $transition-standard;
        }
      }

      &:hover {
        svg {
          path {
            fill-opacity: 0.67;
          }
        }

        border: none;
      }
    }
  }
}

.replyMessage {
  display: flex;
  min-height: 38px;
  justify-content: flex-start;
  margin-bottom: 6px;


  .deleteMessage {
    display: flex;
    align-items: center;
    @include font-style($f-family-sf-text, italic, 400, 14px, 24px, -0.154px, $white-60);
  }

  .bar {
    width: 3px;
    background: #FFFFFF;
    opacity: 0.35;
    border-radius: 6px;
    top: 0;
    left: 0;
    margin-right: 12px;

  }

  .replyMessage__container {
    @include font-style($f-family-sf-text, italic, 400, 12px, 18px, auto, $white-70);
    display: flex;
    flex-direction: column;

    .content {
      @include font-style($f-family-mono, normal, 400, 14px, 24px, auto, $white-90);
    }
  }

}

.chat__container {
  display: flex;
  flex-direction: column-reverse;
  padding: 0 20px;
  background: white;
  overflow: auto;
  position: relative;
  height: calc(100vh - 180px);

  .userImg {
    transition: $transition-standard;
    transform: translateX(0);
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-right: 10px;
    @extend %flex-element-center;
    @include font-style($f-family-sf-display, normal, 500, 22px, 24px, 0.35px, $white-100);
  }

  .chat__messageContainer {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    transition: $transition-standard;
    margin-bottom: 16px;
    transform: translateX(0);

    .messageDate {
      background: white;
    }

    span {
      @include font-style($f-family-sf-text, normal, 400, 12px, 18px, auto, $black-50);

    }
  }

  .chat__containerAvatar {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #afb4c5;
    color: black;
    font-weight: 600;
  }

  .chat__containerItem {
    display: flex;
    justify-content: flex-end;
    position: relative;
    align-items: center;
  }

  .chat__containerSelectedImg {
    position: absolute;
    background: transparent;
    width: 22px;
    height: 22px;
    border: 2px solid rgba(0, 0, 0, 0.2);
    border-radius: 50%;
    left: 0;
    transition: 0.2s linear;
    transform: translateX(-50px);
    @extend %flex-element-center;

    img {
      width: 22px;
      height: 22px;
      transition: 0.2s linear;
      opacity: 0;
    }
  }


  .chat__containerMessageText {
    position: relative;
    padding: 10px 30px 10px 16px;
    background-color: $black-80;
    display: flex;
    flex-direction: column;
    width: 100vw;
    max-width: 260px;
    word-break: break-word;
    align-items: flex-start;
    @include font-style($f-family-mono, normal, 400, 14px, 24px, -0.01em, $white-90);


    .forwardedMessage {
      @include font-style($f-family-sf-text, normal, 400, 14px, 24px, -0.154px, rgba(255, 255, 255, 0.9));
      margin-bottom: 6px;
    }

    img {
      position: absolute;
      right: 9px;
      bottom: 8px;
    }

    .editedIcon {
      position: absolute;
      left: -30px;
      top: 50%;
      transform: translateY(-50%);
    }
  }

  .dayOfSendMessage {
    display: flex;
    justify-content: center;
    @include font-style($f-family-sf-text, normal, 400, 14px, 24px, -0.154px, $black-50);
    margin-bottom: 8px;
  }

  .unreadMessage {
    position: relative;
    @extend %flex-element-center;
    margin-bottom: 8px;
    margin-top: 8px;

    div {
      position: absolute;
      z-index: 5;
      @include font-style($f-family-sf-text, normal, 400, 14px, 24px, -0.154px, $black-50);
      padding: 0 16px;
      background: white;
    }

    span {
      top: 50%;
      position: absolute;
      z-index: 0;
      width: 100%;
      height: 0.5px;
      background-color: $black-50;
    }
  }

  .pinnedMessage {
    @extend %flex-element-center;
    @include font-style($f-family-sf-text, normal, 400, 14px, 24px, -0.154px, $black-50);
    margin-bottom: 8px;
  }

}

.movieWithMessage__container {
  position: absolute;
  display: flex;
  background: #F2F2F0;
  flex-direction: row !important;
  align-items: center;
  border-top-right-radius: 12px;
  border-top-left-radius: 12px;
  border-bottom: 0.5px solid rgba(0, 0, 0, 0.12);
  padding: 10px 15px 8.5px;
  bottom: 60px;
  left: 16px;
  z-index: 1000000;
  width: 466px;

  button {
    border: none;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    padding: 0 !important;
    cursor: pointer;
    background: transparent;
    display: flex;
    margin-left: auto;
    align-items: center;
    justify-content: center;

    svg {
      width: 20px;
      height: 20px;

      path {
        transition: $transition-standard;
      }
    }

    &:hover {
      svg {
        path {
          fill-opacity: 0.67;
        }
      }

      border: none;
    }
  }

  img {
    width: 20px;
    height: 20px;
    margin-right: 15px;
  }

}

.loader__containerInButton {
  background: transparent;
  @extend %flex-element-center;
  margin-left: auto;
  margin-right: auto;

  .lds_dual_ring {
    display: inline-block;
    width: 20px;
    height: 20px;
    @extend %flex-element-center;

    &:after {
      content: " ";
      display: block;
      width: 15px;
      height: 15px;
      border-radius: 50%;
      border: 3px solid #fff;
      border-color: #fff transparent #fff transparent;
      animation: lds-dual-ring 1.2s linear infinite;
    }
  }

  @keyframes lds-dual-ring {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }

}

.loader__container {
  @extend %flex-element-center;
  height: calc(100vh - 50px);
  background-color: transparent;
  border-radius: 24px;
  width: 550px;
  margin-left: auto;
  margin-right: auto;

  .lds_ripple {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;

    div {
      &:nth-child(2) {
        animation-delay: -0.5s;
      }

      position: absolute;
      border: 4px solid $black-80;
      opacity: 1;
      border-radius: 50%;
      animation: lds_ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
    }
  }

  @keyframes lds_ripple {
    0% {
      top: 36px;
      left: 36px;
      width: 0;
      height: 0;
      opacity: 0;
    }
    4.9% {
      top: 36px;
      left: 36px;
      width: 0;
      height: 0;
      opacity: 0;
    }
    5% {
      top: 36px;
      left: 36px;
      width: 0;
      height: 0;
      opacity: 1;
    }
    100% {
      top: 0;
      left: 0;
      width: 72px;
      height: 72px;
      opacity: 0;
    }
  }
}

.chat__container_noSearches {

  position: relative;
  background-color: white;
  @extend %flex-element-center;
  height: calc(100vh - 180px);

