.messageField__container {
  padding: 10px 16px 16px 16px;
  display: flex;

  button {
    min-width: 44px;
    height: 44px;
    background-color: #F2F2F0;
    border-radius: 12px;
    @extend %flex-element-center;
    border: none;
    cursor: pointer;
    transition: $transition-standard;
    margin-left: 8px;

    &:not([disabled]):hover {
      background: #EBEBE8;
    }

    svg {
      path {
        transition: $transition-standard;
      }
    }

    &:disabled {
      cursor: auto;

      svg {
        path {
          fill-opacity: 0.5;
        }
      }
    }

  }

  input {
    width: 100%;
    @include font-style($f-family-sf-text, normal, 400, 14px, 24px, -0.154px, $black-90);
    border: none;
    padding: 10px 16px;
    transition: $transition-standard;
    background: $light-gray;
    border-radius: 12px;
    height: 44px;


    &::placeholder {
      transition: $transition-standard;
      @include font-style($f-family-sf-text, normal, 400, 14px, 24px, -0.154px, $black-50);
    }

    &:focus {
      outline: none;

      &::placeholder {
        @include font-style($f-family-sf-text, normal, 400, 14px, 24px, -0.154px, $black-40);
      }


    }
  }

}


